<template>
    <div class="report d-flex flex-column" style="border: 1px solid lightblue; box-sizing: border-box;">
        
           

            <v-flex>
                <!-- <v-img src="img/report-bg.png" ></v-img> -->
                <v-img src="data:image/png;base64," ></v-img>
                <table class="ma-3 mt-5" style="border: 1px solid lightgray; width: calc(100% - 1.5rem);">
                    <tbody>
                        <tr class="ma-2">
                            <td>Name:</td>
                            <td>{{ report.PatientName }}</td>
                        </tr>
                        <tr class="ma-2">
                            <td>Lab No.:</td>
                            <td>{{ report.LabNumber }}</td>
                        </tr>
                        <tr class="ma-2" v-if="report.tests.length">
                            <td>Registration Date:</td>
                            <td>{{ moment(report.tests[0].DateTimeCollected).format('dddd YYYY-MM-DD hh:mm A') }}</td>
                        </tr>
                    </tbody> 
                </table>
            </v-flex>

            <table class="ma-3" style="border: 1px solid lightgray; width: calc(100% - 1.5rem);">
                <thead>
                    <tr style="background: lightgray">
                        <th>Test</th>
                        <!-- <th>Colection</th> -->
                        <th>Reporting date</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(test, index) in report.tests" :key="index" class="ma-2">
                        <td style="width:30rem;">{{ test.TestName }}</td>
                        <!-- <td>{{ moment(test.DateTimeCollected).format('dddd DD/MM hh:mm A') }}</td> -->
                        <td>{{  moment(test.ReportingTime).format('dddd YYYY-MM-DD hh:mm A') }}</td>
                    </tr>
                </tbody> 
            </table>          
            
            <!-- <ul>
                <li v-for="(test, index) in report.tests" :key="index">
                    {{ test.TestName }}
                    <ul>
                        <li>{{ test.DateTimeCollected }}</li>
                    </ul>
                </li>
            </ul> -->
                


    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { PrintSubject } from '@/globals';

// import DonorResult from './DonorResult';
// import Mirror from './Mirror';

export default {
    name: "Report",
    // components: {
    //     Mirror,
    //     DonorResult
    // },

    computed: {

      ...mapGetters(['printMode', 'schedulingData']),

    },

    watch: {
        schedulingData() {

            const minutesOfDay = m => parseInt(m.substr(3,2)) + parseInt(m.substr(0,2)) * 60;

            this.schedulingData.shifts.sort( (a,b) => {
                return minutesOfDay(a.collectionTime) - minutesOfDay(b.collectionTime);
            });  

            this.schedulingData.runs.sort( (a,b) => {
                return minutesOfDay(a.collectionTime) - minutesOfDay(b.collectionTime);
            });

            let runs = [];
            ///////////////////////////////////////
            // spread runs to decompress days /////
            ///////////////////////////////////////
            for( let run of this.schedulingData.runs ) {
                // let arr = run.days.split('').filter( (x, index) => x == 1 ).map( x => ({
                let arr = run.days.split('').map( (x, index) => ({
                    day: this.days[index],
                    active: x,
                    ...run
                }));

                runs = [...runs, ...arr.filter( x => x.active == 1 )]

                // console.log('arr', arr);
            }

            console.log(runs);
            this.runs = runs;

        },

        report() {

            const minutesOfDayCollection = m => m.getMinutes() + m.getHours() * 60;
            const minutesOfDayShift = m => parseInt(m.substr(3,2)) + parseInt(m.substr(0,2)) * 60;

            for ( let test of this.report.tests ) {

                //////////////////////////////////////
                // get test scheduling data //////////
                //////////////////////////////////////
                let scheduling = this.schedulingData.tests.find( x => x.testID == test.TestID );
                if ( scheduling ) test.scheduling = scheduling;
                else test.scheduling = {
                    byShift: true
                };

                let dateCollected = new Date(test.DateTimeCollected);

                let day = this.days[dateCollected.getDay()];

                // alert(JSON.stringify(test.scheduling, undefined, 2));

                if ( test.scheduling.byShift ) {

                    let done = false;

                    let added = 0;

                    while( !done ) {
                        for ( let shift of this.schedulingData.shifts.filter( x => x.day == day) ) {

                            if( added > 0 || minutesOfDayCollection(dateCollected) < minutesOfDayShift(shift.collectionTime) ) {

                                test.ReportingTime = shift.day + ' ' + shift.reportingTime;
                                test.added = added;

                                let newDate = this.moment(dateCollected).add(added, 'days');
                                newDate = this.moment(newDate).format('YYYY-MM-DD') + " " + shift.reportingTime;
                                // fix timezone difference
                                // test.ReportingTime = this.moment(newDate).subtract(2, 'hours');
                                test.ReportingTime = this.moment(newDate);

        
                                done = true;
                                break;
                            }
                        }

                        if( !done ) {
                            added += 1;
                            let idx = this.days.indexOf(day);
                            idx += 1;
                            if( idx == 7 ) idx = 0;
                            day = this.days[idx];
                        }
                    }
                } else if ( test.scheduling.byRun ) {

                    let done = false;

                    let added = 0;

                    while( !done ) {
                        for ( let run of this.runs.filter( x => x.day == day) ) {

                            if( added > 0 || minutesOfDayCollection(dateCollected) < minutesOfDayShift(run.collectionTime) ) {

                                test.ReportingTime = this.moment(dateCollected).add(added, 'days').add(parseInt(run.reportingLag), 'hours');
                                
                                done = true;
                                break;
                            }
                        }

                        if( !done ) {
                            added += 1;
                            let idx = this.days.indexOf(day);
                            idx += 1;
                            if( idx == 7 ) idx = 0;
                            day = this.days[idx];
                        }
                    }
                } else {

                    // test.scheduling.hours = 6;

                    let reportingTime = this.moment(dateCollected).add(parseInt(test.scheduling.hours), 'hours');

                    // alert(reportingTime);
                    // alert(this.moment(reportingTime).format("YYYY-MM-DD") + " 22:30:00");

                    // check if reporting date is before 9 AM in the same day
                    // or after 10:30 PM in the same day

                    const fromTime = this.moment((this.moment(reportingTime).format("YYYY-MM-DD") + " 09:00:00"));

                    let diff = reportingTime.diff(fromTime);
                    let diffDuration = this.moment.duration(diff);
                    
                    if ( diffDuration.minutes() < 0 ) {
                        
                        reportingTime = fromTime;
                        
                    } else {

                        const toTime = this.moment((this.moment(reportingTime).format("YYYY-MM-DD") + " 22:30:00"));

                        diff = reportingTime.diff(toTime);
                        diffDuration = this.moment.duration(diff);

                        if ( diffDuration.minutes() > 0 ) reportingTime = this.moment(fromTime).add(1, 'days');

                    }

                    test.ReportingTime = reportingTime; 

                    
                    // const now = moment("2017-01-26T14:21:22+0000");
                    // const expiration = moment("2017-01-25T17:24:22+0000");
                    // // get the difference between the moments
                    // const diff = expiration.diff(now);

                    // //express as a duration
                    // const diffDuration = moment.duration(diff);
                    // // display
                    // console.log("Days:", diffDuration.days());
                    // console.log("Hours:", diffDuration.hours());
                    // console.log("Minutes:", diffDuration.minutes());




                    //test.ReportingTime = this.moment(dateCollected).add(parseInt(test.scheduling.hours), 'hours');
                }
                
            }
        }
    },

    data: () => ({
        runs: [],
        PrintSubjectSubscribtion: false,
        PrintSubject,

        report: {
            tests: []
        },
        days: [],
        shifts: [],
    }),

    methods: {
        beforeprint() {
            // alert(this.printMode);
            this.$forceUpdate();
        },
    },

    created() {
        window.addEventListener("beforeprint", this.beforeprint);
        this.days = this.moment.weekdays(); //.map( day => day );

         this.PrintSubjectSubscribtion = this.PrintSubject.subscribe({
           next: order => {
              this.report = order;
              console.log(order);
              setTimeout( () => window.print() );
            }
        });
    },

    destroyed() {
        window.removeEventListener("beforeprint", this.beforeprint);
        this.PrintSubjectSubscribtion.unsubscribe(); 
    }

}
</script>

<style scoped>
