            text-align: center;
            position: relative;
            clear: both;
            height: calc(60% - 0.1rem);
            width: 100%;
            /* background: #cfdfe4; */
            background: #e0e1e2 none; 
            margin-bottom: 0.2rem;
            overflow: hidden;
            white-space: nowrap;
            direction: rtl;
            max-width: 4.5rem;
            overflow: hidden;
        }
        .data-cell div:nth-child(2) {
            /* color: red; */
            font-family: monospace;
            font-size: 0.75rem!important;
            text-align: center;
            position: relative;
            clear: both;
            width: 100%;
            height: calc(40% - 0.1rem);
            /* background: #e6e6e0;
            background: #e6e6e0a6; */
            overflow: hidden;
            white-space: nowrap;
            /* max-width: 4rem; */
            max-width: 4.5rem;
            overflow: hidden;
        }

        tr {
            height: 4rem!important;
        }
        .image {
            width: 101px;
            height: 71px;
            position: absolute;
            top: 1rem;
            left: 2.5rem;
            /* background-image: url(data:image/png;base64,); */
        }
        .signature-line {
            display: flex;
            flex: 2rem;
            /* background: red; */
            border-bottom: 1px dotted lightgray;
            font-size: 1.15rem;
            line-height: 2;
            margin-top: .5rem;
            width: 100%;
        }

        .overlay {
            position: absolute;
            top: 55.5mm;
            left: 3mm;
            width: 200.5mm;
            height: 156.5mm;
            display: flex!important;
            flex-direction: column;

            top: 67mm;
            left: 16mm;
            width: 187.5mm;
            height: 145mm;
        }
        .cell-line {
            display: flex!important;
            flex-direction: row!important;
            width: 100%;
            flex: 1;
        }
        .cell-block {
            display: flex!important;
            flex-direction: row!important;
            width: 100%;
            flex: 1;
            display: flex;
            flex-direction: column!important;
            /* padding: .75mm!important; */
            text-align: center;
        }
        .cell-block-data {
            flex: 1;
        }

    }


table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  text-align: left;
  padding: 8px;
}

tr {height: 2.5rem!important;}

tr:nth-child(even) {background-color: #f2f2f2;}
</style>
